







import { Component, Prop, Vue } from 'vue-property-decorator'

interface HelloWorldProps {
    msg: string
    alert: () => void
}

@Component
export default class HelloWorld<HelloWorldProps> extends Vue {
    @Prop() private msg!: string
    data() {
        return {
            message: 'hello world!'
        }
    }
    onClick(e: Event) {
        this.$emit('alert', e)
    }
}
